@import "https://fonts.googleapis.com/css2?family=DM+Sans:ital,wght@0,400;0,500;0,700;1,400;1,500;1,700&display=swap";
*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

section {
  width: 375px;
  margin-left: auto;
  margin-right: auto;
}

.container {
  border: 1px solid #00f;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 375.89px) and (max-width: 768px) {
  section {
    width: 375px;
  }
}

@media screen and (min-width: 768.89px) and (max-width: 1440px) {
  section {
    width: 768px;
  }

  .container {
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1440.89px) {
  section {
    width: 1440px;
  }

  .container {
    padding-left: 24px;
    padding-right: 24px;
  }
}

.book-card {
  width: 335px;
  border: 1px solid green;
  margin: 0 0 40px;
}

.book-category {
  color: #b4afaf;
  text-transform: uppercase;
  letter-spacing: .03em;
  margin-bottom: 18px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.28571;
}

.book-cover {
  border-radius: 8px;
  margin-bottom: 14px;
  overflow: hidden;
}

.book-name {
  text-transform: uppercase;
  letter-spacing: -.04em;
  margin-bottom: 4px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.125;
}

.book-author {
  font-size: 12px;
  font-weight: 400;
  line-height: calc(14px / 12px);
  color: #b4afaf;
  letter-spacing: -.04em;
  margin-bottom: 8px;
  font-style: italic;
}

.book-card-btn {
  text-transform: uppercase;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #4f2ee8;
  border-radius: 40px;
  margin-left: auto;
  padding: 14px 28px;
  display: block;
}

.section-title {
  letter-spacing: -.04em;
  margin-bottom: 40px;
  font-size: 32px;
  line-height: 1.1875;
}

.sponsors {
  height: 352px;
  width: 335px;
  background: radial-gradient(183.6% 183.01% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  border-radius: 16px;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 24px 40px 68px;
  display: flex;
  position: relative;
  overflow: hidden;
}

.js-sponsors-list {
  position: absolute;
  top: 0;
  left: 0;
}

.sponsor-img {
  filter: brightness(0) invert();
}

.sponsor-item {
  flex-direction: row;
  align-items: center;
  gap: 14px;
  margin-bottom: 20px;
  display: flex;
}

.sponsors-slideUP__btn, .sponsors-slideDown__btn {
  width: 30px;
  height: 30px;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin-top: 16px;
  margin-left: auto;
  margin-right: auto;
  font-size: 10px;
  display: block;
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
}

.sponsors-slideDown__btn.hide, .sponsors-slideUP__btn.hide {
  display: none;
}

.sponsors-title {
  letter-spacing: -.04em;
  margin-bottom: 40px;
  font-size: 28px;
  font-weight: 700;
  line-height: 1.14286;
}

.overlay {
  width: 100%;
  height: 100%;
  margin: 0;
  position: relative;
  overflow: hidden;
}

.sponsors-component {
  margin-bottom: 40px;
}

.book-category__list-item {
  letter-spacing: -.02em;
  color: rgba(17, 17, 17, .6);
  margin-bottom: 24px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.125;
}

.book-category__list-item:last-child {
  margin-bottom: 0;
}

.book-category__list-item:hover, .book-category__list-item:focus, .active {
  color: #4f2ee8;
  text-transform: uppercase;
  font-weight: 700;
}

.book-category__container {
  padding-bottom: 40px;
}

